<template>
  <v-app
    id="app"
    style="padding-top: 65px; width: 1050px"
  >
    <div
      class="Bag-order"
      style="width: 80%; margin: 0 auto; border-bottom: 2px solid black"
    >
      <h2
        class="tit"
        style="padding-bottom: 34px"
      >
          
      </h2>
    </div>

    <v-form v-model="valid">
      <div style="width: 80%; margin: 0 auto; padding-bottom: 20px">
        <table style="width: 100%; border-bottom: 2px solid #dddfe1;">
          <h3 style="padding-top: 10px">
            
          </h3>
          <br>

          <search-place @childEvent="getEmitData" />
          <br>
          <div>: {{ lodging.title }}</div>
          <v-img
            :src="lodging.firstimage2"
            width="200px"
            height="150px"
            alt=""
          />
          <br>
        </table>
      </div>

      <div style="width: 80%; margin: 0 auto; padding-bottom: 20px">
        <table style="width: 100%; border-bottom: 2px solid #dddfe1;">
          <h3>  </h3>
          <br>

          <v-card
            v-for="(item, index) in bagType"
            :key="index"
            style="margin-top: 10px"
          >
            <v-card-text>
              <v-row align="center">
                {{ item.title }}
                <v-spacer />
                {{ item.value }}
                <v-checkbox
                  v-model="checkedName"
                  :value="item.value"
                />
              </v-row>
            </v-card-text>
          </v-card>

          <v-card style="margin: 20px 0">
            <v-card-text>
               : {{ bagAmount }} 
            </v-card-text>
          </v-card>
          <br>
        </table>
      </div>

      <div style="width: 80%; margin: 0 auto; padding-bottom: 20px">
        <table style="width: 100%; border-bottom: 2px solid #dddfe1;">
          <h3></h3>
          <br>
          <div style="padding-left: 15%">
            <div style="width: 50%; display: inline-block">
              <DateTimePicker
                :label="''"
                @child="resultDate"
              />
            </div>
            <div style="width: 50%; display: inline-block">
              <DateTimePicker
                :label="''"
                @child="resultDate"
              />
            </div>
          </div>
          <br>
        </table>
      </div>

      <div style="width: 80%; margin: 0 auto; padding-bottom: 20px">
        <table style="width: 100%; border-bottom: 2px solid black;">
          <h3></h3>
          <br>
          <v-textarea
            v-model="ordRequest"
            style="padding-bottom: 10px"
            name="input-7-1"
            label=" (255 )"
            hint="Hint text"
          />
        </table>
      </div>




      <!--      <div style="width: 80%;margin: 0 auto; padding-top: 20px">-->
      <!--        <v-btn style="float: right;" @click="addOrder"> </v-btn>-->
      <!--      </div>-->
      <div style="width: 80%;margin: 0 auto; padding-top: 20px">
        <button
          style="float: right;"
          @click="addOrder"
        >
           
        </button>
      </div>
    </v-form>
  </v-app>
</template>

<script>
import DateTimePicker from "@/views/bag/order/DateTimePicker"
import axios from "axios";
import SearchPlace from "@/components/SearchPlace";

export default {
  components: {
    SearchPlace,
    // MapComponent
    DateTimePicker
  },
  data() {
    return {
      sDate: '',
      panel: [0, 1],
      disabled: false,
      readonly: false,
      lodging: '',
      checkedName: [],
      overlay: false,
      valid: '',
      checkBagTime: '',
      // 
      dateTimePicker: '',
      //
      entrustTime: '',
      //
      withdrawTime: '',
      //
      entrustAddress: '',
      //
      withdrawAddress: '',
      //
      ordRequest: '',
      //
      image2:'',

      bagType: [
        {title: ' (57cm )', value: 11000},
        {title: ' (57cm  67cm )', value: 16000},
        {title: ' (67cm   20kg )', value: 20000},
        {title: ' (40L   10kg )', value: 10000},
        {title: ' (40L   10kg )', value: 15000},
        {title: ' ', value: 30000}
      ],
    }
  },
  computed: {
    bagAmount() {
      var a = 0;
      this.checkedName.forEach(i => {
        a = a + i;
      })
      console.log(a)
      return a;
    },
  },
  watch: {
    overlay(val) {
      val && setTimeout(() => {
        this.overlay = false
      }, 2000)
    },
  },

  methods: {
    resultDate(sDate) {
      this.sDate = sDate
      console.log(this.sDate)
      return this.sDate
    },

    getEmitData: function (lodging) {
      this.lodging = lodging
      console.log("" + this.lodging)
    },

    addOrder() {
      let storageBag = {
        ord_id: '',
        ord_amount: this.bagAmount, //
        user_id: this.$store.state.user.userId, // UserId
        keep_start: this.lodging.pl_id, //
        entrust_time: this.sDate,  //
        withdraw_time: this.sDate,  //
        ord_selection: '', //
        ord_request: this.ordRequest,
        status:''
      }
      axios
        .post('/api/storageAddOrder', storageBag)
        .then((res) => {
          console.log(storageBag)
          alert('!')
        })
    },
    startAddress(address) {
      this.entrustAddress = address
      console.log(this.entrustAddress)
    },

  },
}
</script>

<style scoped>

button {
  border: 1px solid black;
  background-color: white;
  color: black;
  display: inline-block;
  width: 120px;
  vertical-align: top;
  height: 44px;
  border-radius: 3px;
  font-weight: 700;
  font-size: 14px;
  line-height: 40px;
  text-align: center;
  outline: none;
}

.Bag-order {
  margin: 10px;
  font-size: xx-large;
}

.tit{
  font-weight: 700;
  font-size: 28px;
  line-height: 35px;
  color: #333;
  letter-spacing: -1px;
}

</style>
