<template>
  <v-app>
    <v-card>
      <v-col>
        <h1></h1>
      </v-col>
      <v-row style="width: 300px;">
        <v-col style="width: 150px">
          <v-select
            v-model="check"
            label=" "
            :items="dropdown_edit"
            item-value="text"
          />
        </v-col>

        <v-col
          v-if="check==''"
          style="width: 150px"
        >
          <v-select
            v-model="storageStatusCheck"
            label=""
            :items="storageStatus"
            item-value="text"
          />
        </v-col>

        <v-col
          v-else
          style="width: 150px"
        >
          <v-select
            v-model="transportStatusCheck"
            label=""
            :items="transportStatus"
            item-value="text"
          />
        </v-col>
      </v-row>
    </v-card>

    <div>
      <template v-if="check == ''">
        <h3></h3>
        <v-data-table
          :headers="storageHeaders"
          :items="sortedStorageList"
          :items-per-page="5"
        />
      </template>

      <template v-else>
        <h3></h3>
        <v-data-table
          :headers="transportHeaders"
          :items="sortedTransportList"
          :items-per-page="5"
        />
      </template>
    </div>
  </v-app>
</template>

<script>
import axios from "axios";

export default {
  name: "TrackingView",
  data() {
    return {
      check: '',
      storageStatusCheck: '',
      transportStatusCheck:'',
      storageStatus: [
        {text: '', value: ''},
        {text: '', value: ''},
        {text: '', value: ''},
      ],
      transportStatus:[
        {text: '', value: ''},
        {text: '', value: ''},
        {text: '', value: ''},
      ],

      dropdown_edit: [
        {text: '', value: 'storage'},
        {text: '', value: 'transport'},

      ],
      storageHeaders: [
        {text: '', align: 'start', sortable: false, value: 'ord_id'},
        {text: '', value: 'ord_amount'},
        {text: '', value: 'title'},
        {text: '', value: 'entrust_time'},
        {text: '', value: 'withdraw_time'},
        {text: '', value: 'status'}
      ],
      transportHeaders: [
        {text: '', align: 'start', sortable: false, value: 'ord_id'},
        {text: '', value: 'ord_amount'},
        {text: '', value: 'keep_start'},
        {text: '', value: 'keep_end'},
        {text: '', value: 'entrust_time'},
        {text: '', value: 'withdraw_time'},
        {text: '', value: 'status'}
      ],
      methods: {},
      storageList: [],
      transportList: [],
      keys: [],
    }
  },
  computed:{
    sortedStorageList(){
      return this.storageList.filter( it=> it.status===this.storageStatusCheck)
    },
    sortedTransportList(){
      return this.transportList.filter( it=> it.status===this.transportStatusCheck)
    }
  },
  mounted() {
    this.a()
    this.b()
  },
  methods: {
    a() {
      axios.get("/api/bagStorage/" + this.$store.state.user.userId)
        .then((res) => {
          this.storageList = res.data;
          console.log(' :' + res.data)
        })
    },
    b() {
      axios.get("/api/bagTransport/" + this.$store.state.user.userId)
        .then((res) => {
          this.transportList = res.data;
          console.log(' :' + res.data)
        })
    }
  }
}
</script>

<style scoped>


.tit{
  font-weight: 700;
  font-size: 28px;
  line-height: 35px;
  color: #333;
  letter-spacing: -1px;
}
</style>
