<template>
  <div>
    <h1></h1>
    <v-col
      class="d-flex"
      cols="12"
      sm="10"
    />
    <v-select
      v-model="inq_type"
      :items="items"
      label=""
      outlined
    />

    <v-text-field
      v-model="user_id"
      type="text"
      label=""
    />

    <v-text-field
      v-model="inq_title"
      type="text"
      label=""
    />

    <v-textarea
      v-model="inq_body"
      type="text"
      label=""
      maxlenght="5"
    />


    <v-row style="width:50%">
      :::1 <v-file-input elevation="5">
        
      </v-file-input><br>
    </v-row>
    <v-row style="width:50%">
      :::2 <v-file-input></v-file-input>
    </v-row>

    <div id="button">
      <v-btn
        type="button"
        color="pink"
        @click="check"
      >
        
      </v-btn>
      <v-btn
        type="button"
        @click="cancel"
      >
        
      </v-btn>
    </div>

    <!--    <p>{{user_id}}</p>-->
  </div>
</template>

<script>

import axios from "axios";

export default {
  name: "WritingView",
  data(){
    return{
      inq_type: '',
      inq_title: '',
      inq_body: '',
      user_id:this.$store.state.user.userId,
      items:['','','', '']
    }
  },
  mounted() {
    axios.get('/api/data/'+ this.$store.state.user.userId)
      .then(res=>{
        this.user_id = res.data.user_id
        console.log(res.data)

      }).catch((error)=>{
        this.error=error
    })
  },

  methods: {
    check() {
      console.log(this.user_id)
      if (!this.inq_title){
        alert("  ");
        return;
      }

      var sendform = new FormData()
      console.log(this.user_id)
      sendform.append("inq_type", this.inq_type)
      sendform.append("user_id", this.user_id)
      sendform.append("inq_title", this.inq_title)
      sendform.append("inq_body", this.inq_body)
      console.log(sendform)
      // sendform.get("inq_type")

      // sendform.append("user_phone", this.user_phone)

      axios({
        method:'post',
        url: '/api/inquiry/Writing',
        headers:{
          "Content-Type" : 'multipart/form-data', //   
        },
        data:sendform,
      })
        .then((res) => {
          console.log(res.data)
          if (res.data === true) {
            alert(' ..');
            this.$router.push('/')
          }
          else{
            alert(' ')
          }
        })
    },
    cancel() {
      this.$router.push('/Questions' + '')
    },
    bookmark() {
      this.$router.push('/BookMark')
    },
    pay() {
    },
    myData() {
      this.$router.push('/MyData')
    },
    questions() {
      this.$router.push('/Questions')
    },
  }
}
</script>

<style scoped>
#button{
  position: center;
  text-align: center;
}
</style>

