<template>
  <div class="address">
    <div>
       : <input
        v-model="postcode"
        type="text"
        placeholder=""
      >
      <input
        type="button"
        value=" "
        @click="execDaumPostcode()"
      ><br>
    </div>

    <div>
      <input
        v-model="address"
        type="text"
        placeholder=""
      ><br>
    </div>
    <input
      id="detailAddress"
      v-model="detailAddress"
      type="text"
      placeholder=""
    >
  </div>
</template>

<script>

export default {
  props:{

  },
  data() {
    return {
      postcode: "",
      address: "",
      extraAddress: "",
      detailAddress: "",
    };
  },
  methods: {
    execDaumPostcode() {
      new window.daum.Postcode({
        oncomplete: (data) => {

          if (this.extraAddress !== "") {
            this.extraAddress = "";
          }
          if (data.userSelectedType === "R") {
            //     
            this.address = data.roadAddress;
          } else {
            //     (J)
            this.address = data.jibunAddress;
          }

          //       .
          if (data.userSelectedType === "R") {
            //    . ( )
            //     "//" .
            if (data.bname !== "" && /[||]$/g.test(data.bname)) {
              this.extraAddress += data.bname;
            }
            //  ,   .
            if (data.buildingName !== "" && data.apartment === "Y") {
              this.extraAddress +=
                this.extraAddress !== ""
                  ? `, ${data.buildingName}`
                  : data.buildingName;
            }
            //    ,     .
            if (this.extraAddress !== "") {
              this.extraAddress = `(${this.extraAddress})`;
            }
          } else {
            this.extraAddress = "";
          }
          //  .
          this.$emit('addressData',this.address );
          this.postcode = data.zonecode;
        },
      }).open();

    },

  },
};
</script>
